
  const postData = async (url, data) => { // стрелочная функция
    const res = await fetch(url, {
      method: "POST",
      headers: {
        'Content-type': 'application/json'
      },
      body: data
    });

    return await res.json(); // возвращаем промис чтобы работать с ним дальше
  }; // ASYNC AWAIT(урок) выводитм в функцию обращение к серверу

  const getResource = async (url) => { // создаем функцию с get запросом
    const res = await fetch(url);

    if (!res.ok) {
      throw new Error(`Couln't fetch ${url}, status: ${res.status}`); // создаем ошибку и "выкидываем" её
    }

    return await res.json();
  };    

  export {postData};
  export {getResource};