function exp() {
  // левый зарпос
  fetch('http://localhost:3000/menu')
    .then(data => data.json())
    .then(res => console.log(res));

  // Constructors - тренировка

  function User(name, id) {
    this.name = name;
    this.id = id;
    this.human = true;
    this.hello = function () {
      console.log('hi');
    }
  }

  const vasya = new User('vasya', 'suka');

  vasya.hello();

  console.log(vasya);

  // Классы - тренировка

  class Username {
    constructor(name, id) {
      this.name = name;
      this.id = id;
      this.human = true;
    }
    hello() {
      console.log('hello');
    }
    exit() {
      console.log('exit');
    }
  }
}

export default exp;